*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  scroll-snap-type: y mandatory;
}

.App{
  background-color: black;
  height: 100vh; 
  display: grid;
  place-items: center;
}

  .app__videos{
    height: 100vh;
    max-height: 800px;
    width: 80%;
    max-width: 500px;
    border: 1px solid black;
    border-radius: 20px;
    overflow: scroll;
    scrollbar-width: none;
    scroll-snap-type: y mandatory;
    position: relative;
  }
  /*esconde o scroll bar no chrome, safari e opera*/
  .app__videos::-webkit-scrollbar{
display: none;
  }
    /*esconde o scroll bar no internet explore, edge e firefox*/
.app__videos{
  -ms-overflow-style: none;
  scrollbar-width: none;
}


@media only screen and (max-width: 440px){
  .app__videos{
    width: 100%;
    height: 100vh;
  }
}

